<script lang="jsx">
export default {
  name: "Teleport",
  props: {
    to: {
      type: Element,
      required: true,
    },
    disabled: {
      type: Boolean,
      default: true,
    },
  },
  inject: ["parent"],
  watch: {
    disabled() {
      if (this.disabled) {
        // DOM 
        this.to.appendChild(this.$el);
      } else {
        //  DOM
        this.parent.toSourceDom(this.$el);
      }
    },
  },
  mounted() {
    if (this.disabled) document.querySelector(this.to).appendChild(this.$el);
  },
  /**
   *  render{}
   *  this.$scopedSlots.default() 
   */
  render() {
    return <div className="Teleport">{this.$scopedSlots.default()}</div>;
  },
};
</script>
<style scoped>
.Teleport {
  width: 100%;
  height: 100%;
}
</style>
