// (scale)
const getGridSize = (scale) => {
  if (scale <= 0.25) return 40;
  if (scale <= 0.5) return 20;
  if (scale <= 1) return 10;
  if (scale <= 2) return 5;
  if (scale <= 4) return 2;
  return 1;
};

const FONT_SCALE = 0.83; // 10 / 12

export const drawHorizontalRuler = (ctx, start, shadow, options) => {
  const { scale, width, height, canvasConfigs } = options;
  const { bgColor, fontColor, shadowColor, ratio, longfgColor, shortfgColor } =
    canvasConfigs;

  // ctx, 
  ctx.scale(ratio, ratio);
  ctx.clearRect(0, 0, width, height);

  // 1. 
  ctx.fillStyle = bgColor;
  ctx.fillRect(0, 0, width, height);

  // 2. 
  if (shadow) {
    const shadowX = (shadow.x - start) * scale; // 
    const shadowWidth = shadow.width * scale; // 
    ctx.fillStyle = shadowColor;
    ctx.fillRect(shadowX, 0, shadowWidth, (height * 3) / 8);
  }

  const gridSize = getGridSize(scale); // 
  const gridPixel = gridSize * scale;
  const gridSize_10 = gridSize * 10; // 
  const gridPixel_10 = gridSize_10 * scale;

  const startValue = Math.floor(start / gridSize) * gridSize; // (start, gridSize)
  const startValue_10 = Math.floor(start / gridSize_10) * gridSize_10; // (start, gridSize_10)

  const offsetX = ((startValue - start) / gridSize) * gridPixel; // ctx(start)px
  const offsetX_10 = ((startValue_10 - start) / gridSize_10) * gridPixel_10; // ctx(start)px
  const endValue = start + Math.ceil(width / scale); // ()

  // 3. ()
  ctx.beginPath(); // ,clearRect,

  ctx.fillStyle = fontColor;
  ctx.strokeStyle = longfgColor;

  // forfordom
  // 
  for (
    let value = startValue_10, count = 0;
    value < endValue;
    value += gridSize_10, count++
  ) {
    const x = offsetX_10 + count * gridPixel_10 + 0.5; // prevent canvas 1px line blurry
    ctx.moveTo(x, 0);
    ctx.save();
    ctx.translate(x, height * 0.4);
    ctx.scale(FONT_SCALE / ratio, FONT_SCALE / ratio);
    ctx.fillText(value, 4 * ratio, 7 * ratio);
    ctx.restore();
    ctx.lineTo(x, (height * 9) / 16);
  }
  ctx.stroke();
  ctx.closePath();

  // 
  ctx.beginPath();
  ctx.strokeStyle = shortfgColor;
  for (
    let value = startValue, count = 0;
    value < endValue;
    value += gridSize, count++
  ) {
    const x = offsetX + count * gridPixel + 0.5; // prevent canvas 1px line blurry
    ctx.moveTo(x, 0);
    if (value % gridSize_10 !== 0) {
      ctx.lineTo(x, (height * 1) / 4);
    }
  }
  ctx.stroke();
  ctx.closePath();

  // ctx matrix
  ctx.setTransform(1, 0, 0, 1, 0, 0);
};

export const drawVerticalRuler = (ctx, start, shadow, options) => {
  const { scale, width, height, canvasConfigs } = options;
  const { bgColor, fontColor, shadowColor, ratio, longfgColor, shortfgColor } =
    canvasConfigs;

  // ctx, 
  ctx.scale(ratio, ratio);
  ctx.clearRect(0, 0, width, height);

  // 1. 
  ctx.fillStyle = bgColor;
  ctx.fillRect(0, 0, width, height);

  // 2. 
  if (shadow) {
    // 
    const posY = (shadow.y - start) * scale;
    // 
    const shadowHeight = shadow.height * scale;
    ctx.fillStyle = shadowColor;
    ctx.fillRect(0, posY, (width * 3) / 8, shadowHeight);
  }

  const gridSize = getGridSize(scale); // 
  const gridPixel = gridSize * scale;
  const gridSize_10 = gridSize * 10; // 
  const gridPixel_10 = gridSize_10 * scale;

  const startValue = Math.floor(start / gridSize) * gridSize; // (start, gridSize)
  const startValue_10 = Math.floor(start / gridSize_10) * gridSize_10; // 

  const offsetY = ((startValue - start) / gridSize) * gridPixel; // ctx(start)px
  const offsetY_10 = ((startValue_10 - start) / gridSize_10) * gridPixel_10; // ctx(start)px
  const endValue = start + Math.ceil(height / scale); // ()

  // 3. ()
  ctx.beginPath(); // ,clearRect,

  ctx.fillStyle = fontColor;
  ctx.strokeStyle = longfgColor; // 

  for (
    let value = startValue_10, count = 0;
    value < endValue;
    value += gridSize_10, count++
  ) {
    const y = offsetY_10 + count * gridPixel_10 + 0.5;
    ctx.moveTo(0, y);
    ctx.save(); // 
    ctx.translate(width * 0.4, y); // 
    ctx.rotate(-Math.PI / 2); //  -90 
    ctx.scale(FONT_SCALE / ratio, FONT_SCALE / ratio); // 10px
    ctx.fillText(value, 4 * ratio, 7 * ratio); // 
    // 
    ctx.restore();
    ctx.lineTo((width * 9) / 16, y);
  }
  ctx.stroke(); // 
  ctx.closePath(); // 

  ctx.beginPath(); // 
  ctx.strokeStyle = shortfgColor;

  for (
    let value = startValue, count = 0;
    value < endValue;
    value += gridSize, count++
  ) {
    const y = offsetY + count * gridPixel + 0.5;
    ctx.moveTo(0, y);
    if (value % gridSize_10 !== 0) {
      ctx.lineTo((width * 1) / 4, y);
    }
  }
  ctx.stroke();
  ctx.closePath();

  // ctx matrix
  ctx.setTransform(1, 0, 0, 1, 0, 0);
};
